import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { ArtRegisterComponent } from './pages/art-register/art-register.component';
import { SpecComponent } from './pages/spec/spec.component';
import { RmMasterComponent } from './pages/rm-master/rm-master.component';

const routes: Routes = [
    { path: '', redirectTo: '/rmmaster', pathMatch: 'full' },
    { path: 'artregister', component:ArtRegisterComponent },
    { path: 'firstpage', component:AppComponent },
    { path: 'rmmaster', component:RmMasterComponent },
    { path: 'spec', component:SpecComponent }
];

@NgModule({
    imports: [ RouterModule.forRoot(routes) ],
    exports: [ RouterModule ]
})

export class AppRoutingModule {}


