import { BrowserModule } from '@angular/platform-browser';
import { APP_BASE_HREF } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { DevExtremeModule } from 'devextreme-angular';

import { AppComponent } from './app.component';
import { ArtRegisterComponent } from './pages/art-register/art-register.component';

import { Company, Service } from './app.service';

import { AppRoutingModule } from './routing.module';
import { SpecComponent } from './pages/spec/spec.component';
import { RmMasterComponent } from './pages/rm-master/rm-master.component';

@NgModule({
  declarations: [
    AppComponent,
    ArtRegisterComponent,
    SpecComponent,
    RmMasterComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    DevExtremeModule,
    AppRoutingModule
  ],
  providers: [{provide: APP_BASE_HREF, useValue: '/juh'} ,Service],
  // providers: [Service],
  bootstrap: [AppComponent]
})
export class AppModule { }



