import { Component } from '@angular/core';
import { Company, Service } from '../../app.service';

import notify from 'devextreme/ui/notify';

@Component({
  moduleId: module.id,
  selector: 'app-art-register',
  templateUrl: './art-register.component.html',
  styleUrls: ['./art-register.component.css']
})
export class ArtRegisterComponent {

  companies: Company[];
  itemCount: number;
  
  constructor(service: Service) {
    this.companies = service.getCompanies();
    this.itemCount = this.companies.length;
  }
  title = 'art-register';

  alertFunction() {
    notify('Please select Dept!!', 'error', 2000);
    //alert("test");
  }

}



